html {
    background-color: #17171d;
}

h1 {
    color: rgb(186, 191, 196);
    margin: 0 0 8px 30px;
    padding-top: 24px;
}

h3 {
    color: rgb(186, 191, 196);
    margin-left: 12px;
}

iframe {
    width: calc(100% - 40px);
    height: 321px;
    border: 0;
    margin: 20px 20px 20px;
    border-radius: 15px;
}

input {
    padding-top: 9px !important;
    padding-bottom: 10px !important;
    text-align: center;
}

.app {
    min-height: 100vh;
}

.troubleshooting {
    position: absolute;
    opacity: 1;
    width: calc(100% - 50px);
    max-width: 300px;
    top: 115px;
    left: 25px;
    height: 321px;
    color: rgb(186, 191, 196);
    z-index: -1;
    animation: show-ts 6s;
}

@keyframes show-ts {
    0% {
        opacity: 0
    }
    50% {
        opacity: 0
    }
    100% {
        opacity: 1
    }
}

.all-form-inputs {
    min-width: 180px !important;
    margin-left: 30px !important;
    margin-right: 30px !important;
    margin-bottom: 12px !important;
}


.freq {
    width: 60px !important;
    margin-left: 20px !important;
    margin-right: 20px !important;
    margin-bottom: 12px !important;
    margin-top: -3px !important;
}


#full-screen {
    width: calc(100% - 40px);
    margin: 20px;
    color: rgb(186, 191, 196)
}

.big-container {
    border: 1px solid #323238;
    margin-right: 20px;
    margin-left: 20px;
    margin-bottom: 20px;
    border-radius: 12px;
}

.s-container {
    border-radius: 15px;
    border: 1px solid #31366d;
    width: fit-content;
    display: inline-block;
    margin: 12px;
    padding: 15px 15px 3px;
}

.f-container {
    border-radius: 15px;
    width: fit-content;
    display: inline-block;
    vertical-align: top;
    margin: 0 25px 0 25px;
}

.share-container {
    border-radius: 15px;
    float: right;
    margin: 0 25px 0 25px;
    width: 310px;
}

.footer-p {
    color: #babfc4;
    font-size: 15px;
}

.a-footer {
    text-decoration: none;
}

.footer {
    width: 100%;
    min-height: 230px;
    background-color: #040409;
    color: white;
    border-top-left-radius: 20px;
    border-top-right-radius: 20px;
    margin-top: 50px;
}

.music-text {
    margin-right: 20px;
    margin-left: 20px;
    position: relative;
    top: 6px;
}

.react-share__ShareButton {
    margin: 6px;
}

.right-when-large {
    float: right;
}

@media (max-width: 1400px) {

    .right-when-large {
        float: unset;
    }
}


@media (min-width: 1110px) {
    .text-margin {
        height: 0;
        margin: 0;
    }
}

@media (min-height: 650px) {
    .sticky-canvas {
        position: -webkit-sticky;
        position: sticky;
        top: 0;
        left: 0;
        width: 100%;
        z-index: 11;
        background-color: #17171d;
        box-shadow: 0 2px 5px -2px #040409;
        margin-bottom: 12px;
    }

    .troubleshooting {
        top: 40px;
    }
}

@media (max-width: 640px) {

    .bottom-button {
        width: 100%;
    }

    label {
        width: 100%;
    }

    .all-form-inputs {
        width: calc(100vw - 220px);
    }

    .freq {
        width: 100% !important;
        margin-left: 0 !important;
        margin-top: -15px !important;
    }

    h1 {
        text-align: center;
    }


    .share-container {
        width: 100%;
        margin-right: 0;
    }

}

@media (pointer: none), (pointer: coarse) {
    .freq {
        display: none !important;
    }
}